@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap");
.sendbird-create-channel--content {
  width: 480px;
  max-height: 552px; }

.sendbird-create-channel--scroll {
  height: 360px;
  overflow-y: scroll; }

.sendbird-create-channel--user-row {
  height: 56px;
  width: 472px;
  position: relative;
  align-items: center;
  display: flex; }
  .sendbird-theme--light .sendbird-create-channel--user-row {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12); }
  .sendbird-theme--dark .sendbird-create-channel--user-row {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12); }
  .sendbird-create-channel--user-row .sendbird-create-channel--user-row--label {
    padding-left: 56px;
    max-width: 320px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .sendbird-create-channel--user-row .sendbird-create-channel--user-row--avatar {
    position: absolute;
    left: 0;
    top: 12px; }
  .sendbird-create-channel--user-row .sendbird-create-channel--user-row--checkbox {
    position: absolute;
    right: 0; }

.sendbird-create-channel__footer__close {
  width: 80px; }

.sendbird-create-channel__footer__ok {
  width: 80px; }
