@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap");
.sendbird-members-accordion {
  padding: 8px 16px 16px 16px;
  box-sizing: border-box; }
  .sendbird-theme--light .sendbird-members-accordion {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    background-color: #FFFFFF; }
  .sendbird-theme--dark .sendbird-members-accordion {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    background-color: #161616; }
  .sendbird-members-accordion .sendbird-members-accordion__member {
    position: relative;
    padding: 12px 0px 12px 44px;
    height: 40px;
    box-sizing: border-box; }
    .sendbird-members-accordion .sendbird-members-accordion__member .sendbird-label {
      max-width: 240px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis; }
  .sendbird-members-accordion .sendbird-members-accordion__member-avatar {
    position: absolute;
    left: 0;
    top: 10px; }
  .sendbird-members-accordion .sendbird-members-accordion__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 16px; }

.sendbird-more-members__popup-member {
  position: relative;
  padding: 17px 56px;
  box-sizing: border-box; }
  .sendbird-theme--light .sendbird-more-members__popup-member {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    background-color: #FFFFFF; }
  .sendbird-theme--dark .sendbird-more-members__popup-member {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    background-color: #161616; }
  .sendbird-more-members__popup-member .sendbird-more-members__popup-avatar {
    position: absolute;
    left: 0;
    top: 8px; }
  .sendbird-more-members__popup-member .sendbird-label {
    max-width: 320px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }

.sendbird-more-members__popup-scroll {
  max-height: 420px;
  overflow-y: scroll; }
